.number-keys-layout {
  display: inline-grid;
  grid-template-columns: repeat(4, 1fr);
 
  -ms-flex-align: stretch;
 
      align-items: stretch;
  grid-gap: 10px 10px;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  justify-items: stretch;
}

.number-keys-layout>span {
  display: -ms-flexbox;
  display: flex;
  pointer-events: all;
  font-size: inherit;
  height: 70px;
  width: 70px;
  -ms-flex-item-align: center;
      align-self: center;
  justify-self: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.number-keys-layout>span.number-keys--plus {
  grid-row: 2 / 4;
  grid-column: 4 / 5;
  height: 150px
}

.number-keys-layout>span.number-keys--zero {
  grid-row: 5 / 6;
  grid-column: 1 / 3;
  width: 150px;
}

.number-keys-layout>span.number-keys--enter {
  grid-row: 4 / 6;
  grid-column: 4 / 5;
  height: 150px
}


